import React from 'react';
import { graphql } from 'gatsby';
const _ = require('underscore');

const Categories = ({ data }) => {
    const nodes = data.allMarkdownRemark.edges;
    const categorias = {};

    nodes.forEach(({ node }) => {
        if (_.has(categorias, node.frontmatter.categorias[0])) {
            _.uniq(categorias[node.frontmatter.categorias[0]].tags.push(...node.frontmatter.tags));
        } else {
            categorias[node.frontmatter.categorias[0]] = {
                tags: []
            }
            categorias[node.frontmatter.categorias[0]].tags = node.frontmatter.tags;
        }
    })

    return (
        <div>
            {categorias.Faculdade.tags.map((tag, index) => (
                <span className="tag is-info is-medium" key={index}>{tag}</span>
            ))}
        </div>
    )
};

export default Categories;

export const query = graphql`
  query CategoriesQuery {
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          frontmatter {
            tags
            categorias
          }
        }
      }
    }
  }
`;
